<template>
  <v-layout row wrap>
    <v-flex xs2>
      <v-card flat>
        <v-card-title>
          <v-toolbar color="grey lighten-4" flat>
            <v-toolbar-title>Form Controls</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
        </v-card-title>
        <v-card-text>
          <v-layout row wrap>
            <v-flex xs12>
              <v-list dense>
                <draggable 
                v-model="formControls" 
                :options="{sort: false, group: {name: 'fields', pull: 'clone', put: false}}" 
                style="min-height: 100px"
                :clone="onCloned"
                class="pb-5">
                  <v-container v-for="(formControl, i) in formControls" :key="i" draggable="true">
                    <v-list-tile avatar @click="">
                      <v-list-tile-avatar>
                        <v-icon>{{ formControl.icon }}</v-icon>
                      </v-list-tile-avatar>
                      <v-list-tile-content>
                        <v-list-tile-title>{{formControl.description}}</v-list-tile-title>
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-container>
                </draggable>
              </v-list>
            </v-flex>
          </v-layout>
        </v-card-text>
      </v-card>
    </v-flex>
    <v-flex xs10>
      <v-card :v-if="!loading" flat color="grey lighten-2">
        <v-card-title>
          <v-toolbar color="grey lighten-2" flat>
            <v-toolbar-title>Drag the Form Controls Below</v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
        </v-card-title>
        <v-card-text>
              <v-container>
                <draggable element="v-layout" row wrap
                v-model="form.fields" 
                :options="{group: {name: 'fields'}}" 
                style="min-height: 100px; background-color: white;"
                class="pb-5">
                  <v-flex v-for="(field, i) in form.fields" :key="field.id" :class="field.width">
                    <v-layout row>
                      <template v-if="field.type === 'table'">
                        <bs-table-field :label="field.label" 
                        :items="field.value" 
                        :columns="field.tableColumns"
                        :name="field.name"
                        :width="field.width"
                        :canEditField="true"
                        v-on:fieldChanged="editedField => onFieldChanged(field, editedField)"
                        v-on:fieldRemoved="onFieldRemoved(i)"></bs-table-field>
                      </template>
                      <template v-else-if="field.type === 'checkbox'">
                        <bs-checkbox :label="field.label" 
                          :required="field.required" 
                          :checkState="field.value" 
                          :name="field.name"
                          :width="field.width"
                          :canEditField="true"
                          v-on:checkStateChanged="checkState => {field.value = checkState}"
                          v-on:fieldChanged="editedField => onFieldChanged(field, editedField)"
                          v-on:fieldRemoved="onFieldRemoved(i)"></bs-checkbox>
                      </template>
                      <template v-else-if="field.type === 'text'">
                        <bs-text-field :label="field.label" 
                        :required="field.required"
                        :multiLine="field.multiLine"
                        :prefix="field.prefix"
                        :suffix="field.suffix" 
                        :text="field.value" 
                        :name="field.name"
                        :width="field.width"
                        :canEditField="true"
                        :alignText="field.alignText"
                        v-on:textChanged="text => {field.value = text}"
                        v-on:fieldChanged="editedField => onFieldChanged(field, editedField)"
                        v-on:fieldRemoved="onFieldRemoved(i)"></bs-text-field>
                    </template>
                    <template v-else-if="field.type === 'number'">
                        <bs-number-field :label="field.label" 
                        :required="field.required"
                        :multiLine="field.multiLine"
                        :prefix="field.prefix"
                        :suffix="field.suffix" 
                        :number="field.value" 
                        :name="field.name"
                        :width="field.width"
                        :canEditField="true"
                        v-on:numberChanged="number => {field.value = number}"
                        v-on:fieldChanged="editedField => onFieldChanged(field, editedField)"
                        v-on:fieldRemoved="onFieldRemoved(i)"></bs-number-field>
                    </template>
                    <template v-else-if="field.type === 'date'">
                      <bs-date-field :label="field.label" 
                      :date="field.value" 
                      :name="field.name"
                      :width="field.width"
                      :canEditField="true"
                      v-on:dateChanged="date => {field.value = date}"
                      v-on:fieldChanged="editedField => onFieldChanged(field, editedField)"
                      v-on:fieldRemoved="onFieldRemoved(i)"></bs-date-field>
                    </template>
                    <template v-else-if="field.type === 'image'">
                      <bs-image-field :image="field.value" 
                      :label="field.label" 
                      :preview="field.preview"
                      :name="field.name"
                      :width="field.width"
                      :canEditField="true"
                      v-on:imageChanged="image => {field.value = image}" 
                      v-on:fieldChanged="editedField => onFieldChanged(field, editedField)"
                      v-on:fieldRemoved="onFieldRemoved(i)"></bs-image-field>
                    </template>
                    <template v-else-if="field.type === 'radio'">
                      <bs-radio-button-field :label="field.label" 
                      :required="field.required" 
                      :horizontal="field.horizontal" 
                      :selectedOption="field.value" 
                      :options="field.options"
                      :name="field.name"
                      :width="field.width"
                      :canEditField="true"
                      v-on:selectionChanged="option => {field.value = option}"
                      v-on:fieldChanged="editedField => onFieldChanged(field, editedField)"
                      v-on:fieldRemoved="onFieldRemoved(i)"></bs-radio-button-field>
                    </template>
                    <template v-else-if="field.type === 'select'">
                      <bs-select-field :label="field.label" 
                      :selectedOption="field.value" 
                      :options="field.options" 
                      :required="field.required"
                      :multiple="field.multiple"
                      :name="field.name"
                      :width="field.width"
                      :canEditField="true"
                      v-on:selectionChanged="option => {field.value = option}"
                      v-on:fieldChanged="editedField => onFieldChanged(field, editedField)"
                      v-on:fieldRemoved="onFieldRemoved(i)"></bs-select-field>
                    </template>
                    <template v-else-if="field.type === 'label'">
                      <bs-label-field :label="field.label"
                      :width="field.width"
                      :icon="field.labelIcon"
                      :color="field.labelColor"
                      :canEditField="true"
                      v-on:fieldChanged="editedField => onFieldChanged(field, editedField)"
                      v-on:fieldRemoved="onFieldRemoved(i)"></bs-label-field>
                    </template>
                    </v-layout>
                  </v-flex>
                </draggable>
              </v-container>
        </v-card-text>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
import Vue from 'vue'
import draggable from 'vuedraggable'
import BsEditFieldDialog from '@/components/AppBuilder/FormBuilder/FieldEditDialog'
import BsTextField from '@/components/AppBuilder/FormBuilder/TextField'
import BsNumberField from '@/components/AppBuilder/FormBuilder/NumberField'
import BsDateField from '@/components/AppBuilder/FormBuilder/DateField'
import BsImageField from '@/components/AppBuilder/FormBuilder/ImageField'
import BsCheckboxField from '@/components/AppBuilder/FormBuilder/CheckboxField'
import BsTabelField from '@/components/Appbuilder/FormBuilder/TableField'
import BsRadioButtonField from '@/components/Appbuilder/FormBuilder/RadioButtonField'
import BsSelectField from '@/components/Appbuilder/FormBuilder/SelectField'
import BsLabelField from '@/components/AppBuilder/FormBuilder/LabelField'

Vue.use(draggable)

export default {
  props: ['form'],
  data () {
    return {
      menu: null,
      lookupItems: ['Categories', 'Locations', 'Customers', 'Vendors', 'Projects'],
      lookupItemFields: ['Name', 'Category'],
      formControls: [{
        id: 'asdfasdfasdf',
        icon: 'text_fields',
        type: 'text',
        description: 'Text',
        label: 'Text',
        name: 'textField1',
        required: true,
        multiLine: false,
        prefix: '',
        suffix: '',
        width: 'xs12 pa-3',
        calculation: '',
        value: '',
        defaultValue: ''
      },
      {
        id: 'utytfjujkhdjhjhg',
        icon: 'plus_one',
        type: 'number',
        description: 'Number',
        label: 'Number',
        name: 'numberField1',
        required: true,
        prefix: '',
        suffix: '',
        width: 'xs12 pa-3',
        calculation: '',
        value: 0,
        defaultValue: 0
      },
      {
        id: 'afgrefdgfgg',
        icon: 'check_box',
        type: 'checkbox',
        description: 'Checkbox',
        label: 'Checkbox',
        name: 'checkboxField1',
        required: true,
        width: 'xs12 pa-3',
        calculation: '',
        value: false,
        defaultValue: true
      },
      {
        id: 'aasdfasdfeww',
        icon: 'arrow_drop_down_circle',
        type: 'select',
        description: 'Combobox',
        label: 'Combobox',
        name: 'comboxboxField1',
        required: true,
        multiple: false,
        width: 'xs12 pa-3',
        calculation: '',
        value: null,
        defaultValue: null,
        options: [
          'Option 1',
          'Option 2',
          'Option 3'
        ]
      },
      {
        id: 'efeqefasfaf',
        icon: 'event',
        type: 'date',
        description: 'Date',
        label: 'Date',
        name: 'dateField1',
        required: true,
        width: 'xs12 pa-3',
        calculation: '',
        value: null,
        defaultValue: null
      },
      {
        id: 'sfgfqrfqer',
        icon: 'label',
        type: 'label',
        labelColor: 'info',
        labelIcon: null,
        description: 'Label',
        required: false,
        label: 'Some imortant message',
        name: 'label',
        childListFields: [],
        width: 'xs12 pa-3',
        calculation: '',
        value: '',
        defaultValue: ''
      },
      {
        id: 'fasdfasddfasfd',
        icon: 'radio_button_checked',
        type: 'radio',
        description: 'Radio Buttons',
        label: 'Radio Buttons',
        name: 'radioButtonsField1',
        required: true,
        horizontal: false,
        width: 'xs12 pa-3',
        calculation: '',
        value: '',
        defaultValue: '',
        options: [
          'Option 1',
          'Option 2',
          'Option 3'
        ]
      },
      {
        id: 'asdfvqrqew',
        icon: 'insert_photo',
        type: 'image',
        description: 'Image',
        label: 'Image',
        name: 'imageField',
        required: true,
        preview: true,
        width: 'xs12 pa-3',
        calculation: '',
        value: {
          imageUrl: null,
          filename: null
        },
        defaultValue: '',
        imageUrl: null
      },
      {
        id: 'asdfajkwqeioas',
        icon: 'search',
        type: 'lookup',
        description: 'Lookup Field',
        label: 'Lookup Field',
        name: 'lookupField1',
        required: true,
        multiple: false,
        lookupList: null,
        lookupListFields: [],
        width: 'xs12 pa-3',
        calculation: '',
        value: {},
        defaultValue: {}
      },
      {
        id: 'sfgfqrfqer',
        icon: 'reorder',
        type: 'table',
        required: false,
        description: 'Table',
        label: 'Table',
        name: 'table1',
        tableColumns: [],
        width: 'xs12 pa-3',
        alignText: 'text-xs-center',
        calculation: '',
        value: []
      }]
    }
  },
  created () {
    // needed to make value property reactive
    let fields = []
    this.form.fields.forEach(field => fields.push(Object.assign({}, field)))
    this.form.fields.splice(0)
    fields.forEach(field => {
      if (field.type === 'table') {
        field.value = []
      } else {
        field.value = field.defaultValue
      }
      this.form.fields.push(field)
    })
  },
  methods: {
    field (name) {
      return this.form.fields.find(function (field) {
        return field.name === name
      })
    },
    onCancel () {
      this.$router.push('/forms')
    },
    onSave () {
      this.$store.dispatch('saveForm', {id: this.$route.params.id, ...this.form})
    },
    onCloned (original) {
      console.log('cloning')
      const element = {}
      for (var key in original) {
        if (original.hasOwnProperty(key)) {
          element[key] = original[key]
        }
      }
      element.id = Date.now()
      return element
    },
    removeOption (field, item) {
      field.options.splice(field.options.indexOf(item), 1)
    },
    onOk (obj) {
      this.form.fields.splice(obj.index, 1, Object.assign({}, obj.field))
    },
    onValueChanged (field) {
      // eslint-disable-next-line
      eval(field.calculation)
    },
    onItemValueChanged (field, item, prop) {
      const column = field.tableColumns.find(column => {
        return column.name === prop
      })
      // eslint-disable-next-line
      eval(column.calculation)
    },
    onItemAdded (field, item) {
      console.log(item)
      field.value.push(item)
    },
    onItemRemoved (field, item) {
      const index = field.value.indexOf(item)
      field.value.splice(index, 1)
    },
    onFieldChanged (field, editedField) {
      console.log('field changed')
      console.log(field)
      console.log(editedField)
      for (const key in field) {
        if (field.hasOwnProperty(key) && editedField.hasOwnProperty(key)) {
          field[key] = editedField[key]
        }
      }
    },
    onFieldRemoved (index) {
      this.form.fields[index].tableColumns = []
      this.form.fields.splice(index, 1)
    }
  },
  computed: {
    loading () {
      return this.$store.getters.loading
    }
  },
  components: {
    'draggable': draggable,
    'bs-image-field': BsImageField,
    'bs-date-field': BsDateField,
    'bs-text-field': BsTextField,
    'bs-checkbox': BsCheckboxField,
    'bs-number-field': BsNumberField,
    'bs-table-field': BsTabelField,
    'v-edit-field': BsEditFieldDialog,
    'bs-radio-button-field': BsRadioButtonField,
    'bs-select-field': BsSelectField,
    'bs-label-field': BsLabelField
  }
}
</script>



